              <img style="width: 28px" src="../../static/search.png">
            </button>
          </div>
        </div>
      </div>

      <div style="width: 100%;background: #fff;margin-top: 20px">
        <div style="padding: 26px 30px;overflow: hidden">
          <div style="margin-bottom: 60px;font-size: 14px;line-height: 22px">
            <el-divider content-position="left">Before You register</el-divider>
            <div style="margin-bottom: 10px">
              Is HENS only for storing an Ethereum classic address?
              <br>
              No, you can store the addresses of other blockchains, a content hash of a decentralized website, profile
              information such as an avatar and Twitter handle, and more.
            </div>
            <div style="margin-bottom: 10px">
              Can I use an HENS name to point to my website? <br>
              Though HENS can technically store anything, there aren't many third party tools and applications which
              resolve IP addresses attached to HENSThe first ones to support HENS are HebeWallet and HebeSwap
              <br>
              Instead, we suggest hosting your static html/css/images on IPFS and put the hash in your HENS name's
              Content record.
            </div>
            <div style="margin-bottom: 10px">
              What is the maximum length of a name I can register? <br>
              You can only register the name length greater than 1.
            </div>
            <div style="margin-bottom: 10px">
              Can you have names with emojis?<br>
              Yes.
            </div>
          </div>
          <div style="margin-bottom: 60px;font-size: 14px;line-height: 22px">
            <el-divider content-position="left">When you register</el-divider>
            <div style="margin-bottom: 10px">
              At step 1, the transaction was slow so I speeded up<br>
              Our app cannot currently detect that you sped up the transaction. Please refresh the page and start from
              step 1 again.
            </div>

          </div>
          <div style="margin-bottom: 60px;font-size: 14px;line-height: 22px">
            <el-divider content-position="left">After you register</el-divider>
            <div style="margin-bottom: 10px">
              What is a Resolver?<br>
              A Resolver is a smart contract that holds records. Names are set by default to the Public Resolver managed
              by the HENS team and has all the standard HENS record types. You can set your Resolver to a custom
              resolver
              contract if you,d like.
            </div>
            <div style="margin-bottom: 10px">
              How does HENS realize the binding of name and address? What other attributes can be added besides the
              binding address? Can it be modified?<br>
              In smart contracts, there is a data structure called struct, which is a bit similar to the concept of
              class in java language. We design a struct called Record in the contract. Record has two attributes of
              address data type, one is to record the user The resolver that binds the data, one is the owner of the
              recorded data. At the same time, we use mapping to maintain a set of key-value relationships, and bind
              name to Record one-to-one. Therefore, the binding between name and owner is the only record corresponding
              to name, and there is only owner in the only record. We also performed reverse binding, using mapping,
              address as Key, and name as Value. These two sets of relationships will be generated at the same time when
              users register for HENS. We record all the attributes that users can currently bind in Resolver.sol. The
              currently supported attributes include 100+ chain address, IPFS link, Email, avatar, url external
              link, description introduction, notice announcement , Keywords tags, personal account information
              comGithub, comReddit, comTwitter orgTelegram. These records can be arbitrarily modified by the owner. Of
              course, if you are a developer and you want to implement some custom content, you can deploy a contract
              that implements the Resolver interface as your own private data parser.
            </div>
            <div style="margin-bottom: 10px">
              How do I transfer my name?<br>
              For a ".etc" name, transfer both the Registrant and the Controller to the new Ethereum Classic account.
              Since ".etc" names are ERC721 compliant NFTs, you can change the Registrant by simply transferring the NFT
              from any NFT compliant wallet/marketplace as well.<br>
              Note that transferring the ownership (aka the Registrant) of the name does not change the controller nor
              records, so the recipient may need to update them once received. If the recipient is not experienced or
              you prefer your address not to be associated to the transferring names, it may be a good idea for you to
              set the ETC Address record to their Ethereum Classic address, set the controller, then transfer the name.
            </div>
