<template>
  <div>
    <div class="head">
      <div class="content">
        <el-col :xs="20" :sm="10">
          <div style="float: left">
               <span v-if="addr!=''">
            {{subname(addr)}}
          </span>
            <span v-if="addr==''">
             {{$g.chainName}}
          </span>
          </div>
          <div style="margin-top:-2px;float: left;margin-left: 20px">
            <el-button v-if="addr==''" @click="login" size="small"
                       style="width: 100px;background: transparent !important;">
              Connect
            </el-button>
            <el-button v-if="addr!=''" @click="loginout" size="small"
                       style="width: 100px;background: transparent !important;">
              Disconnect
            </el-button>
          </div>
        </el-col>
        <el-col :xs="4" :sm="14">
          <div style="float: right">
            <el-dropdown class="showmemu" @command="handleCommand">
             <span class="el-dropdown-link">
                Menu<i class="el-icon-arrow-down el-icon--right"></i>
             </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="home">Home</el-dropdown-item>
                <el-dropdown-item command="market">Market</el-dropdown-item>
                <el-dropdown-item command="my" v-if="addr!=''">My Account</el-dropdown-item>
                <el-dropdown-item style="color: #409EFF;" command="sellfn" v-if="addr!=''&&hensname!=''">
                  <span v-if="sellnum==0">Sell</span>
                  <span v-if="sellnum!=0">Update Sell</span>
                </el-dropdown-item>
                <el-dropdown-item style="color: #409EFF;" command="cancelsellfn"
                                  v-if="addr!=''&&hensname!=''&&sellnum!=0">
                  Cancel Sell
                </el-dropdown-item>
                <el-dropdown-item command="withdraw" v-if="addr!=''">Withdraw</el-dropdown-item>
                <el-dropdown-item command="listfn">List</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <div class="showmemu1">
              <div style="float: right;padding: 4px 10px" @click="listfn">
                List
              </div>
              <div style="float: right;padding: 4px 10px" v-if="addr!=''" @click="$g.openwithdraw(_this)">
                Withdraw
              </div>
              <div style="float: right;padding: 4px 10px;color: #409EFF;" @click="cancelsellfn"
                   v-if="addr!=''&&hensname!=''&&sellnum!=0">
                Cancel Sell
              </div>
              <div style="float: right;padding: 4px 10px;color: #409EFF;" v-if="addr!=''&&hensname!=''" @click="sellfn">
                <span v-if="sellnum==0">Sell</span>
                <span v-if="sellnum!=0">Update Sell</span>
              </div>
              <div style="float: right;padding: 4px 10px" @click="market">
                Market
              </div>
              <div style="float: right;padding: 4px 10px" v-if="addr!=''" @click="my">
                My Account
              </div>
              <a href="./">
                <div style="float: right;padding: 4px 10px">
                  Home
                </div>
              </a>
            </div>
          </div>

        </el-col>
      </div>
    </div>
    <div class="details">
      <div v-loading="marketloading" style="width: 100%;background: #fff;margin-top: 20px">
        <div class="marketpd">
          <div style="padding-bottom: 10px">
            <el-row :gutter="24">
              <el-col :xs="24" :sm="8" style="padding-bottom: 20px;">
                <div class="marketBorder">
                  <div style="text-align: center;padding:12px;padding-bottom: 0px">
                    <el-image
                      style="width:100%"
                      :src="'https://json.hens.domains/hens/'+sellmodel.tokenid+'/hens.jpg'"
                      :fit="'cover'">
                    </el-image>
                  </div>
                </div>
              </el-col>
              <el-col :xs="24" :sm="16" style="padding-bottom: 20px;">
                <div class="marketxItem--title">{{sellmodel.name}}</div>
                <div style=" font-size: 14px;margin-top: 10px;color: rgb(112, 122, 131);">
                  TokenId
                  <el-link
                    style="margin-top: -4px"
                    :href="'https://blockscout.com/etc/mainnet/token/0x03f4a95d964d364614e514e8638d61cdeed4f8d4/instance/'+sellmodel.tokenid+'/token-transfers'"
                    target="_blank" type="primary">{{sellmodel.tokenid}}
                  </el-link>
                </div>
                <div style=" font-size: 14px;margin-top:2px;color: rgb(112, 122, 131);">
                  Owned by
                  <el-link
                    style="margin-top: -4px"
                    :href="'https://blockscout.com/etc/mainnet/address/'+sellmodel.addr+'/transactions'"
                    target="_blank" type="primary">{{sellmodel.addr}}
                  </el-link>
                </div>
                <div v-if="height!=0" style=" font-size: 14px;margin-top:2px;color: rgb(112, 122, 131);">
                  Last Block {{height}}{{days(height)}}
                </div>
                <div style="margin-top: 20px">
                  <el-card class="box-card">
                    <div style="overflow: hidden">
                      <span style="float: left;margin-right: 2px;margin-top: 10px">Price</span>
                      <img style="width: 20px;margin-top: 12px;float: left"
                           src="../assets/etc.png">
                      <div style="float: left" class="marketxItem--title">{{sellmodel.price}}</div>
                      <div style="float: left;color: rgb(112, 122, 131);margin-top: 14px;margin-left: 10px">
                        (${{parseFloat((sellmodel.price*sellmodel.price_usd).toFixed(2)).toLocaleString()}})
                      </div>
                    </div>
                    <div v-if="sellmodel.price!=0" style="margin-top: 6px">
                      <el-button @click="buyfn"
                                 v-loading="buyloading"
                                 type="primary" plain>Buy Domain Name
                      </el-button>
                    </div>
                    <div style="margin-top: 20px">
                      <el-collapse accordion>
                        <el-collapse-item v-if="textlistis">
                          <template slot="title">
                            <i style="margin-right: 6px;font-size: 22px;" class="el-icon-user"></i>
                            <span style="font-size: 14px">Contact Holder</span>
                          </template>
                          <div>
                            <el-button
                              @click="openherf(item)"
                              v-if="item.val!=''"
                              v-for="item in textlist"
                              :key="item.txt"
                              v-loading="buyloading"
                              type="primary" plain>
                              {{item.txt}}
                            </el-button>
                          </div>
                        </el-collapse-item>
                        <el-collapse-item>
                          <template slot="title">
                            <i style="margin-right: 6px;font-size: 22px;" class="el-icon-data-analysis"></i>
                            <span style="font-size: 14px">Price History</span>
                          </template>
                          <div>
                            <v-chart class="chart" :option="option"/>
                          </div>
                        </el-collapse-item>
                      </el-collapse>
                    </div>
                  </el-card>
                </div>
              </el-col>
            </el-row>
            <div style="margin-top: 20px">
              <el-collapse v-model="activeNames" accordion>
                <el-collapse-item name="1">
                  <template slot="title">
                    <i style="margin-right: 6px;font-size: 22px;" class="el-icon-sort"></i>
                    <span style="font-size: 14px">Item Activity</span>
                  </template>
                  <div>
                    <div>
                      <el-select @change="selectchange" style="width: 100%" v-model="value">
                        <el-option
                          v-for="item in options"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                        </el-option>
                      </el-select>
                    </div>
                    <el-table
                      v-loading="loading"
                      empty-text="No record"
                      :data="list"
                      style="width: 100%">
                      <el-table-column
                        label="Event"
                        width="120">
                        <template slot-scope="scope">
                          {{$g.toA(scope.row.type)}}
                        </template>
                      </el-table-column>
                      <el-table-column
                        label="Price"
                        width="140">
                        <template slot-scope="scope">
                          <div v-if="scope.row.type!='cancel'">
                            <img style="width: 12px;margin-top:5px;float: left"
                                 src="../assets/etc.png">
                            <span style="float: left;font-weight: bold;font-size: 14px">
                              {{scope.row.price}}
                            </span>
                          </div>

                        </template>
                      </el-table-column>
                      <el-table-column
                        label="From"
                        width="340">
                        <template slot-scope="scope">
                          <el-link :href="'https://blockscout.com/etc/mainnet/address/'+scope.row.from+'/transactions'"
                                   target="_blank" type="primary">{{subname1(scope.row.from)}}
                          </el-link>
                        </template>
                      </el-table-column>
                      <el-table-column
                        label="Hash"
                        width="340">
                        <template slot-scope="scope">
                          <el-link
                            :href="'https://blockscout.com/etc/mainnet/tx/'+scope.row.hash+'/internal-transactions'"
                            target="_blank" type="primary">{{subnamehash(scope.row.hash)}}
                          </el-link>
                        </template>
                      </el-table-column>
                      <el-table-column
                        label="BlockNumber"
                        width="240">
                        <template slot-scope="scope">
                          {{scope.row.blockNumber}}
                        </template>
                      </el-table-column>
                    </el-table>
                    <div style="text-align: center;margin-top: 20px">
                      <el-pagination
                        background
                        layout="prev, pager, next"
                        :pager-count="5"
                        :page-size="8"
                        :total="sum"
                        :current-page="currentPage"
                        @current-change="handleSizeChange"
                      >
                      </el-pagination>
                    </div>
                  </div>
                </el-collapse-item>
              </el-collapse>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { use } from 'echarts/core'
  import { CanvasRenderer } from 'echarts/renderers'
  import { LineChart } from 'echarts/charts'
  import { UniversalTransition } from 'echarts/features'

  import {
    GridComponent, TooltipComponent
  } from 'echarts/components'
  import VChart, { THEME_KEY } from 'vue-echarts'

  use([
    GridComponent,
    LineChart,
    CanvasRenderer,
    TooltipComponent,
    UniversalTransition
  ])

  import { Loading } from 'element-ui'

  const web3Utils = require('web3-utils')

  export default {
    name: 'marketx',
    components: {
      VChart
    },
    data() {
      return {
        textlist: [
          {
            txt: 'Url',
            get: 'getUrl',
            set: 'setUrl',
            val: ''
          }, {
            txt: 'Telegram',
            get: 'getOrgTelegram',
            set: 'setOrgTelegram',
            val: ''
          }, {
            txt: 'Notice',
            get: 'getNotice',
            set: 'setNotice',
            val: ''
          }, {
            txt: 'Keywords',
            get: 'getKeywords',
            set: 'setKeywords',
            val: ''
          }, {
            txt: 'IpfsUrl',
            get: 'getIpfsUrl',
            set: 'setIpfsUrl',
            val: ''
          }, {
            txt: 'Email',
            get: 'getEmail',
            set: 'setEmail',
            val: ''
          }, {
            txt: 'Description',
            get: 'getDescription',
            set: 'setDescription',
            val: ''
          }, {
            txt: 'Twitter',
            get: 'getComTwitter',
            set: 'setComTwitter',
            val: ''
          }, {
            txt: 'Reddit',
            get: 'getComReddit',
            set: 'setComReddit',
            val: ''
          }, {
            txt: 'Github',
            get: 'getComGithub',
            set: 'setComGithub',
            val: ''
          }, {
            txt: 'Avatar',
            get: 'getAvatar',
            set: 'setAvatar',
            val: ''
          }
        ],
        options: [
          {
            value: '',
            label: 'All'
          }, {
            value: 'Buy',
            label: 'Buy'
          }, {
            value: 'Sell',
            label: 'Sell'
          }, {
            value: 'Cancel',
            label: 'Cancel'
          }],
        value: '',
        activeNames: '1',
        search: '',
        addr: '',
        hensname: '',
        currentPage: 1,
        sum: 0,
        option: {
          tooltip: {
            trigger: 'axis',
            formatter: function(arg) {
              return arg[0].name + '<br/><div style="margin-top: 4px"><img style="width: 12px;margin-top:4px;float: left"' +
                'src="https://hebeswap.com/assets/0x82A618305706B14e7bcf2592D4B9324A366b6dAd/logo.png">' + arg[0].data + '</div>'
            }
          },
          xAxis: {
            type: 'category',
            data: []
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              data: [],
              type: 'line',
              smooth: true
            }
          ]
        },
        sellmodel: {
          tokenid: 1109,
          name: '',
          addr: '',
          price: 0,
          price_usd: 0
        },
        list: [],
        buyloading: false,
        sellnum: 0,
        _this: '',
        textlistis: false,
        height: 0,
        loading: false,
        marketloading: false

      }
    },
    methods: {
      days(height) {
        let dayblock = 8640
        return parseInt(height / dayblock).toLocaleString() + ' Day'
      },
      openherf(item) {
        let txt = 'Open website'
        if (item.txt == 'Email') {
          txt = 'Copy mail'
        }
        this.$alert(txt + item.val, '', {
          confirmButtonText: 'Yes',
          cancelButtonText: 'No',
          dangerouslyUseHTMLString: true
        }).then(() => {
          if (item.txt != 'Email') {
            window.open(item.val)
          } else {
            // 
            let textarea = document.createElement('textarea')
            document.body.appendChild(textarea)
            textarea.style.position = 'absolute'
            textarea.style.clip = 'rect(0 0 0 0)'
            textarea.value = item.val
            textarea.select()
            document.execCommand('copy', true)
            alert('Copy Success')
          }
        }).catch(() => {

        })
      },
      async sellfn() {
        this.$prompt('Please enter the ETC price to sell', '', {
          confirmButtonText: 'Ok',
          cancelButtonText: 'Cancel'
        }).then(async ({ value }) => {
          let loadingInstance = Loading.service({ fullscreen: true })
          let hash = await this.$g.offerHensForSale(this.txt, value, this.addr)
          if (hash != '') {
            let tt = await this.$g.eth_getTransactionReceipt(hash)
            loadingInstance.close()
            if (tt == '0x0') {
              this.status = 'error'
              this.$message.error('Error')
            } else {
              this.$message({
                message: 'Sell succeeded',
                type: 'success'
              })
            }
          } else {
            loadingInstance.close()
          }
        })
      },
      async cancelsellfn() {
        let loadingInstance = Loading.service({ fullscreen: true })
        let hash = await this.$g.hensNoLongerForSale(this.txt, this.addr)
        if (hash != '') {
          let tt = await this.$g.eth_getTransactionReceipt(hash)
          loadingInstance.close()
          if (tt == '0x0') {
            this.status = 'error'
            this.$message.error('Error')
          } else {
            this.$message({
              message: 'Cancel Sell succeeded',
              type: 'success'
            })
          }
        } else {
          loadingInstance.close()
        }
      },
      async buyfn() {
        if (this.hensname != '') {
          this.$message({
            showClose: true,
            message: 'Only one hens name can be registered per address',
            type: 'error'
          })
          return
        }
        let name = this.sellmodel.name
        name = name.substring(0, (name.length - 4))
        let hash = await this.$g.buyHens(name, this.sellmodel.price, this.addr)
        if (hash != '') {
          this.buyloading = true
          let tt = await this.$g.eth_getTransactionReceipt(hash)
          this.buyloading = false
          if (tt == '0x0') {
            this.status = 'error'
            this.$message.error('Error')
          } else {
            this.$message({
              message: 'Buy succeeded',
              type: 'success'
            })
            this.my()
          }
        }
      },
      selectchange() {
        this.currentPage = 1
        this.txlisthash()
      },
      handleSizeChange(val) {
        this.currentPage = val
        this.txlisthash()
      },
      async invite() {
        this.$router.push({ path: '/invite' })
      },
      tutorial() {
        this.$router.push({ path: '/tutorial' })
      },
      handleCommand(command) {
        if (command == 'sellfn') {
          this.sellfn()
        }
        if (command == 'cancelsellfn') {
          this.cancelsellfn()
        }
        if (command == 'home') {
          window.location = './'
        }
        if (command == 'my') {
          this.my()
        }
        if (command == 'faq') {
          this.faq()
        }
        if (command == 'tutorial') {
          this.tutorial()
        }
        if (command == 'listfn') {
          this.listfn()
        }
        if (command == 'withdraw') {
          this.$g.openwithdraw(this._this)
        }
        if (command == 'market') {
          this.market()
        }
      },
      market() {
        this.$router.push({ path: '/market' })
      },
      async listfn() {
        this.$router.push({ path: '/list' })
      },
      async my() {
        if (this.$route.path != '/search') {
          this.$router.push({ path: '/search', query: { 'address': this.addr } })
        }
      },
      faq() {
        this.$router.push({ path: '/faq' })
      },
      subnamehash(txt) {
        if (txt) {
          if (txt.length > 41) {
            return txt.substring(0, 18) + '...' + txt.substring(50, 66)
          } else {
            return txt
          }
        }
      },
      subname1(txt) {
        if (txt) {
          if (txt.length > 41) {
            return txt.substring(0, 18) + '...' + txt.substring(30, 42)
          } else {
            return txt
          }
        }
      },
      subname(txt) {
        if (this.hensname != '') {
          return this.hensname
        }
        if (txt) {
          if (txt.length > 41) {
            return txt.substring(0, 6) + '...' + txt.substring(30, 42)
          } else {
            return txt
          }
        }
      },
      async searchfn() {
        this.search = this.search.trim()
        if (this.search != '')
          this.$router.push({ path: '/search', query: { url: this.search } })
      },
      async loginout() {
        this.addr = ''
      },
      async login() {
        if (typeof (ethereum) !== 'undefined') {
          this.addr = await this.$g.etclogin()
          if (this.addr != '') {
            this.$g.getNameOfOwner(this.addr).then(async res => {
              this.hensname = res
              this.txt = this.hensname.substring(0, (this.hensname.length - 4))
              let tokenid = await this.$g.getTokenIdOfName(this.txt)
              this.sellnum = await this.$g.henssOfferedForSale(tokenid)
            })
            this.sellmodel.name = await this.$g.getNameOfTokenId(this.sellmodel.tokenid)
            if (this.sellmodel.name != '') {
              this.marketloading = false
              this.$g.isOverLine(this.sellmodel.name).then(res => {
                this.height = parseFloat(res.isOverLine) - parseFloat(res.blockNumber)
              })
              this.$g.getAllProperties(this.sellmodel.name).then(txt => {
                if (txt != '' && txt.split('+').length > 10) {
                  this.textlistis = true
                  this.textlist.forEach((item, index) => {
                    item.val = txt.split('+')[index].trim()
                    try {
                      item.val = web3Utils.hexToString(item.val)
                    } catch (e) {
                      try {
                        item.val = web3Utils.hexToNumber(item.val)
                      } catch (e) {
                        item.val = item.val
                      }
                    }
                    if (item.txt == 'Notice' || item.txt == 'Keywords' || item.txt == 'IpfsUrl' || item.txt == 'Description'
                      || item.txt == 'Avatar') {
                      item.val = ''
                    }
                  })
                } else {
                  this.textlistis = false
                }
              })
              this.$g.getowner(this.sellmodel.name).then(ress => {
                this.sellmodel.addr = ress
              })
              this.$g.henssOfferedForSale(this.sellmodel.tokenid).then(res => {
                this.sellmodel.price = res
              })
              this.$axios.get('https://apis.etcswap.cc/?quotesTxt=etc').then(res => {
                this.sellmodel.price_usd = res.data[0].ticker.price_usd
              })
              this.txlisthash()
              this.loadaxios()
            } else {
              this.$router.push({ path: '/market' })
            }
          }
        } else {
          this.$alert('Please use MateMask / HebeWallet to access, make sure you have connected your wallet', '', {
            confirmButtonText: 'Ok'
          })
        }
      },
      loadaxios() {


        this.$axios.post('https://json.hens.domains/txlistsell', {
          // this.$axios.post('http://127.0.0.1:3666/txlistsell', {
          'key': this.sellmodel.tokenid
        }).then(async res => {

          let blocklist = []
          let priceist = []
          res.data.list.forEach(item => {
            blocklist.push(item.blockNumber)
            priceist.push(item.price)
          })
          this.option.xAxis.data = blocklist
          this.option.series[0].data = priceist

        })
      },
      txlisthash() {
        this.loading = true
        this.$axios.post('https://json.hens.domains/txlisthash', {
          // this.$axios.post('http://127.0.0.1:3666/txlisthash', {
          'tokenid': this.sellmodel.tokenid,
          'num': this.currentPage,
          'type': this.value
        }).then(async res => {
          this.loading = false

          this.list = res.data.list
          this.sum = res.data.count
        })
      }
    },
    mounted() {
      this.marketloading = true
      let tokenid = this.$g.getUrlKey('tokenid')
      if (tokenid != null) {
        this.sellmodel.tokenid = tokenid
        this.login()
      } else {
        this.$router.push({ path: '/market' })
      }
      this._this = this
    }
  }
</script>

<style lang="less">
