    .marketpd {
      padding: 6px 10px !important;
    }

    .chart {
      height: 300px;
      width: 330px !important;
    }

    .marketpd .el-button--primary.is-plain {
      margin-left: 0 !important;
      margin-right: 10px;
      margin-top: 6px;
    }
  }


  .marketxItem--title {
    font-size: 30px;
    font-weight: 600;
    max-width: 100%;
    margin: 0px;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: normal;
    display: -webkit-box;
  }

  .marketBorder {
    border: 1px solid #ebebeb;
    border-radius: 8px;
    cursor: pointer;
    transition: .2s
  }

  .marketBorder:hover {
    box-shadow: 0 0 16px 0 rgba(232, 237, 250, .6), 0 8px 16px 0 rgba(232, 237, 250, .5)
  }

  .effect1 {
    min-height: 100vh;
    -webkit-animation: gradient 7.5s ease-in-out infinite;
    animation: gradient 7.5s ease-in-out infinite;
  }

  .effect1 {
    background: #0299d8;
    background: -webkit-linear-gradient(45deg, #5a3694, #13bdce 33%, #0094d9 66%, #6fc7b5);
    background: -o-linear-gradient(45deg, #5a3694 0, #13bdce 33%, #0094d9 66%, #6fc7b5 100%);
    background: linear-gradient(45deg, #5a3694, #13bdce 33%, #0094d9 66%, #6fc7b5);
    background-size: 400%;
    background-position: 0 100%;
  }

  .detailsactive {
    border-left: 4px solid #67C23A !important;
  }

  .search_txr {
    font-size: 32px;
    color: #dfdfdf;
    text-align: center;
    line-height: 60px;
    height: 60px;
    width: 60px;
    border-radius: 50%;
    border: 2px solid #dfdfdf
    /*border-top: 2px solid #dfdfdf;*/
    /*border-right: 2px solid #dfdfdf;*/
    /*border-left: 2px solid #ff0000;*/
    /*border-bottom: 2px solid #ff0000*/
  }

  .input_el_input__inner {
    .el-input__inner {
      text-align: center !important;
    }
  }

  .tool {
    position: absolute;
    right: 20px;
    top: 14px;
    color: #909399;

    .tool_warp {
      border: 2px solid #409EFF;
      overflow: hidden;
      line-height: 40px;

      div {
        float: left;
        padding: 0px 30px;
        text-align: center;
        font-size: 14px
      }

      .tool_select {
        color: #409EFF
      }
    }
  }

  @media (max-width: 768px) {
    .search h2 {
      font-size: 24px !important;
    }

    .searchBtn {
      width: 24% !important;
      height: 40px !important;
    }

    .searchInput {
      width: 76% !important;
      height: 40px !important;
      font-size: 18px !important;
      text-indent: 2px !important;

    }

    .search {
      width: 94%;
      padding: 2%;
      min-width: auto !important;
    }
  }


  .details {
    .content {
      margin: 20px 0;
      display: flex;

      .left {
        -webkit-box-flex: 1;
        -webkit-flex: 1 1 65%;
        -ms-flex: 1 1 65%;
        flex: 1 1 65%;
        background: #fff;
        border-radius: 10px;
        min-height: 250px;
        margin-right: 20px;
        padding: 20px;
        display: flex;

        .img {
          width: 120px;
          height: 120px;
          background: #f7f7f7;
          border-radius: 8px;
          display: -webkit-box;
          display: -webkit-flex;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-pack: center;
          -webkit-justify-content: center;
          -ms-flex-pack: center;
          justify-content: center;
          -webkit-box-align: center;
          -webkit-align-items: center;
          -ms-flex-align: center;
          align-items: center;
          -webkit-flex-shrink: 0;
          -ms-flex-negative: 0;
          flex-shrink: 0;

          img {
            width: auto;
            height: auto;
            max-width: 85%;
            max-height: 85%;
          }
        }

        .text {
          -webkit-box-flex: 0;
          -webkit-flex-grow: 0;
          -ms-flex-positive: 0;
          flex-grow: 0;
          -webkit-flex-shrink: 1;
          -ms-flex-negative: 1;
          flex-shrink: 1;
          width: 100%;
          padding-left: 20px;
          font-size: 14px;

          p:nth-child(-n+4) {
            line-height: 28px;
            color: #444;
            text-align: left;

            span {
              color: #999;
              padding-right: 5px;
            }
          }

          p:nth-child(-n+4) i {
            padding: 2px 8px;
            font-size: 12px;
            background: #a1a7b7;
            border-radius: 2px;
            margin-right: 10px;
            color: #fff;
          }
        }

        .el-button {
          display: inline-block;
          line-height: 1;
          white-space: nowrap;
          cursor: pointer;
          background: #fff;
          border: 1px solid #dcdfe6;
          -webkit-appearance: none;
          text-align: center;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          outline: none;
          margin: 0;
          -webkit-transition: .1s;
          -o-transition: .1s;
          transition: .1s;
          font-weight: 500;
          -moz-user-select: none;
          -webkit-user-select: none;
          -ms-user-select: none;
          padding: 12px 20px;
          font-size: 14px;
          border-radius: 4px;

          span {
            color: #fff;
            padding-right: 0;
          }
        }

        .el-button--primary {
          color: #fff;
          background-color: #00a8ff;
          border-color: #00a8ff;
        }

        .el-button--medium {
          padding: 10px 20px;
          font-size: 14px;
          border-radius: 4px;
        }
      }

      .right {
        -webkit-box-flex: 1;
        -webkit-flex: 1 0 32%;
        -ms-flex: 1 0 32%;
        flex: 1 0 32%;
        background: #fff;
        border-radius: 10px;
        min-height: 300px;
        padding: 10px 20px;

        a {
          display: -webkit-box;
          display: -webkit-flex;
          display: -ms-flexbox;
          display: flex;
          background: #f7f7f7;
          margin: 10px;
          height: 74px;
          border-radius: 5px;
          -webkit-box-align: center;
          -webkit-align-items: center;
          -ms-flex-align: center;
          align-items: center;
          padding: 0 10px;
          -webkit-touch-callout: none;
          text-decoration: none;
          cursor: pointer;
          -moz-user-select: none;
          -webkit-user-select: none;
          -ms-user-select: none;
          user-select: none;

          img {
            width: 50px;
            border-radius: 50px;
          }

          .text {
            padding-left: 10px;

            h4 {
              color: #444;
              font-size: 16px;
              font-weight: 400;

              span {
                font-size: 12px;
                color: #9c9c9c;
                background: #fff;
                padding: 2px 4px;
                border-radius: 2px;
                margin: 0 5px;
              }
            }

            p {
              color: #999;
              line-height: 24px;
              font-size: 14px;
              height: 24px;
              overflow: hidden;
            }
          }
        }
      }
    }

    max-width: 1180px;
    margin: 60px auto;
    padding-top: 20px;

    .topsearch {
      background: #fff;
      border-radius: 10px;
      overflow: hidden;
      height: 68px;
      line-height: 68px;

      .searchBtn {
        width: 20%;
        height: 60px;
        float: left;
        border-radius: 4px;
        border-top-left-radius: 0px;
        border-bottom-left-radius: 0px;
        background: #409EFF;
        border: #00a8ff;
        color: #282828;
        cursor: pointer;
      }

      .searchInput {
        float: left;
        width: 80%;
        height: 60px;
        text-indent: 15px;
        background: #fff !important;
        color: #444 !important;
        transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
        line-height: 40px;
        outline: none;
        padding: 0 15px;
        display: inline-block;
        box-sizing: border-box;
        border-radius: 4px;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        border: none;
        font-size: 24px;
      }

      .search h2 {
        text-align: center;
        color: #fff;
        -webkit-box-flex: 1;
        -webkit-flex: 1 0 100%;
        -ms-flex: 1 0 100%;
        flex: 1 0 100%;
        padding-bottom: 50px;
        font-size: 44px;
        font-family: "Noto Sans SC";
        font-weight: 100;
      }

      .search {
        -webkit-box-align: center;
        -webkit-align-items: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        z-index: 9;

        margin-top: -15px;
        -webkit-transition: color .5s;
        -o-transition: color .5s;
        transition: color .5s;
      }
    }

    h1 {
      color: #444;
      font-size: 20px;
      line-height: 50px;
      font-weight: 400;
    }

  }

  .title {
    float: left;
    font-size: 16px;
    margin-left: 8px;
    font-family: "Noto Sans SC";
    font-weight: 300;
  }

  .head .headlist {
    float: right;
    font-size: 14px;
    margin-right: 20px;

    .active {
      border-bottom: 1px solid #333435;
    }

    a {
      display: block;
      color: #333435;
      float: left;
      padding: 0 20px;
      -webkit-touch-callout: none;
      text-decoration: none;
      cursor: pointer;
      -moz-user-select: none;
      -webkit-user-select: none;
      -ms-user-select: none;
      user-select: none;

    }

    a:hover {
      color: #909090
    }
  }

  .head .btn {
    font-size: 14px;
    border: 2px solid #00a8ff;
    text-align: center;
    margin-top: 10px;
    height: 36px;
    line-height: 36px;
    border-radius: 42px;
    width: 120px;
    float: right;
    cursor: pointer;
  }

  .head .btn:hover {
    background-color: #00a8ff;
    color: #fff;
  }

  .head .content {
    width: 96%;
    margin: 0 auto;
    max-width: 1800px;
  }

  .head {
    line-height: 60px;
    height: 60px;
    width: 100%;
    background: hsla(0, 0%, 100%, 0.7);
    color: #333435;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    border-bottom: 1px solid #e5e5e5;
  }
</style>



