<template>
  <div class="index effect1">
    <div style="height: 40px;width: 100%;">
      <el-row style="color: #fff;    padding: 20px;">
        <el-col :xs="8" :sm="8">
          <span v-if="addr!=''">
            {{subname(addr)}}
          </span>
          <span v-if="addr==''">
             {{$g.chainName}}
          </span>
          <div style="margin-top: 8px;">
            <el-button v-if="addr==''" @click="login" size="small"
                       style="width: 200px;color: #fff!important;background: transparent !important;">
              Connect
            </el-button>
            <el-button v-if="addr!=''" @click="loginout" size="small"
                       style="width: 200px;color: #fff!important;background: transparent !important;">
              Disconnect
            </el-button>
          </div>
        </el-col>
        <el-col style="text-align: right;cursor: pointer;" :xs="16" :sm="16">
          <el-dropdown class="showmemu" @command="handleCommand">
             <span class="el-dropdown-link" style="color: #fff">
                Menu<i class="el-icon-arrow-down el-icon--right"></i>
             </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="my" v-if="addr!=''">My Account</el-dropdown-item>
              <el-dropdown-item command="market" >Market</el-dropdown-item>
              <el-dropdown-item command="withdraw" v-if="addr!=''">Withdraw</el-dropdown-item>
              <el-dropdown-item command="invite" v-if="addr!=''">Invite</el-dropdown-item>
              <el-dropdown-item command="faq">Faq</el-dropdown-item>
              <el-dropdown-item command="tutorial">Tutorial</el-dropdown-item>

            </el-dropdown-menu>
          </el-dropdown>
          <div class="showmemu1">

            <div style="float: right;padding: 4px 10px" @click="tutorial">
              Tutorial
            </div>
            <div style="float: right;padding: 4px 10px" @click="faq">
              Faq
            </div>
            <div style="float: right;padding: 4px 10px" v-if="addr!=''" @click="invite">
              Invite
            </div>
            <div style="float: right;padding: 4px 10px" v-if="addr!=''" @click="$g.openwithdraw(_this)">
              Withdraw
            </div>
            <div style="float: right;padding: 4px 10px" @click="market">
              Market
            </div>
            <div style="float: right;padding: 4px 10px" v-if="addr!=''" @click="my">
              My Account
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <div>
      <div style="max-width: 1180px; margin: 0 auto;;padding-top:60px;padding-bottom:120px;">
        <div style="padding-bottom:20px;">
          <div style="overflow: hidden;margin-top: 20px;">
            <input v-model="key" class="searchInput" autocomplete="off"
                   placeholder="Search name or address or tokenid"/>
            <button @click="searchFn" class="searchBtn">
              <img style="width: 28px" src="../../static/search.png">
            </button>
          </div>
        </div>
        <el-row :gutter="24" style="text-align: center;">
          <el-col :xs="12" :sm="12" :md="6" :lg="6" v-for="(item,index) in list" :key="index">
            <el-image
              style="width:100%;"
              :src="'https://json.hens.domains/hens/'+item.tokenid+'/hens.jpg'"
              :fit="'contain'"
              :preview-src-list="srcList">
            </el-image>
            <div class="listbtn" style="padding-bottom:10px;font-size: 24px;">
              <el-button  @click="dialogshow(item)" type="text" style="font-size: 24px;color:#fff;">{{item.name}}
              </el-button>
            </div>
          </el-col>
        </el-row>
        <div style="text-align: center;">
          <el-pagination
            background
            layout="prev, pager, next"
            :pager-count="5"
            :page-size="12"
            :total="sum"
            :current-page="currentPage"
            @current-change="handleSizeChange"
          >
          </el-pagination>
        </div>
      </div>
    </div>
    <el-dialog
      title="Info"
      :visible.sync="dialogVisible"
      :width="dialogWidth"
    >
      <span>
        <el-descriptions :column="1" direction="extra">
        <el-descriptions-item label="Id"> <el-link style="margin-top:-4px"
                                                   :href="'https://blockscout.com/etc/mainnet/token/0x03f4a95d964d364614E514e8638d61CDEed4f8D4/instance/'+model.tokenid+'/token-transfers'"
                                                   target="_blank"
                                                   type="primary">{{model.tokenid}}</el-link></el-descriptions-item>
        <el-descriptions-item label="Name">{{model.name}}</el-descriptions-item>
        <el-descriptions-item label="Address"><el-link style="margin-top:-4px"
          :href="'https://blockscout.com/etc/mainnet/address/'+model.addr+'/transactions'" target="_blank"
          type="primary">{{model.addr}}</el-link></el-descriptions-item>
        <el-descriptions-item label="Start Block">{{model.startblock}}</el-descriptions-item>
        <el-descriptions-item label="End Block">{{model.endblock}}</el-descriptions-item>

      </el-descriptions>

      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">Close</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
  export default {
    name: 'HelloWorld',
    data() {
      return {
        addr: '',
        search: '',
        fx: '.etc',
        url: '',
        srcList: [],
        list: [],
        sum: 0,
        currentPage: 1,
        key: '',
        dialogVisible: false,
        model: {},
        formLabelWidth: '50px',
        dialogWidth: '550px',
        hensname:'',
        _this: ''

      }
    },
    methods: {
      setDialogWidth() {
        let windowSize = document.body.clientWidth
        const defaultWidth = 450 // 
        if (windowSize < defaultWidth) {
          this.dialogWidth = '100%'
        } else {
          this.dialogWidth = defaultWidth + 'px'
        }
      },
      dialogshow(item) {
        this.model = item
        console.log(this.model)
        this.dialogVisible = true
      },
      searchFn() {
        this.currentPage = 1
        this.loadaxios()
      },
      handleSizeChange(val) {
        this.currentPage = val
        this.loadaxios()
      },
      handleCommand(command) {
        if (command == 'my') {
          this.my()
        }
        if (command == 'faq') {
          this.faq()
        }
        if (command == 'tutorial') {
          this.tutorial()
        }
        if (command == 'invite') {
          this.invite()
        }
        if (command == 'withdraw') {
          this.$g.openwithdraw(this._this)
        }
        if (command == 'market') {
          this.market()
        }
      },
      market(){
        this.$router.push({ path: '/market' })
      },
      async invite() {
        this.$router.push({ path: '/invite' })
      },
      async loginout() {
        this.addr = ''
      },
      async login() {
        if (typeof (ethereum) !== 'undefined') {
          this.addr = await this.$g.etclogin()
          if (this.addr != '') {
            this.hensname = await this.$g.getNameOfOwner(this.addr)
          }
        }

      },
      subname(txt) {
        if(this.hensname!=''){
          return this.hensname
        }
        if (txt) {
          if (txt.length > 41) {
            return txt.substring(0, 6) + '...' + txt.substring(30, 42)
          } else {
            return txt
          }
        }
      },
      async my() {
        if (this.$route.path != '/search') {
          this.$router.push({ path: '/search', query: { 'address': this.addr } })
        }
      },
      tutorial() {
        this.$router.push({ path: '/tutorial' })
      },
      faq() {
        this.$router.push({ path: '/faq' })
      },
      goto() {
        this.search = this.search.trim()
        if (this.search != '')
          this.$router.push({ path: '/search', query: { url: this.search } })
      },
      loadaxios() {
        this.$axios.post('https://json.hens.domains/mintlists', {
        // this.$axios.post('http://127.0.0.1:3666/mintlists', {
          'key': this.key,
          'num': this.currentPage
        }).then(async res => {
          this.srcList = []
          this.list = []
          this.sum = res.data.count
          res.data.list.forEach(item => {
            item.img = 'https://json.hens.domains/hens/' + item.tokenid + '/hens.jpg'
            this.srcList.push(item.img)
            this.list.push(item)
          })
        })
      }
    },
    async mounted() {
      this.login()
      this.loadaxios()
      this._this = this

      window.onresize = () => {
        return (() => {
          this.setDialogWidth()
        })()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
  .listbtn{
    .el-button {
      word-break:break-all;
      white-space:pre-wrap !important;
    }
  }

  .el-row {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .el-col {
    border-radius: 4px;
  }

  .bg-purple-dark {
    background: #99a9bf;
  }

  .bg-purple {
    background: #d3dce6;
  }

  .bg-purple-light {
    background: #e5e9f2;
  }

  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }

  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }

  .index {

    @media (max-width: 768px) {

      .search h2 {
        font-size: 24px !important;
      }

      .searchBtn {
        width: 24% !important;
        height: 40px !important;
      }

      .searchInput {
        width: 76% !important;
        height: 40px !important;
        font-size: 18px !important;
        text-indent: 2px !important;

      }

      .search {
        width: 94%;
        padding: 2%;
        min-width: auto !important;
      }
    }

    .searchBtn {
      width: 20%;
      height: 80px;
      float: left;
      border-radius: 4px;
      border-top-left-radius: 0px;
      border-bottom-left-radius: 0px;
      background: #409EFF;
      border: #00a8ff;
      color: #282828;
      cursor: pointer;
    }

    .searchInput {
      float: left;
      width: 80%;
      height: 80px;
      text-indent: 15px;
      background: #fff !important;
      color: #444 !important;
      transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
      line-height: 40px;
      outline: none;
      padding: 0 15px;
      display: inline-block;
      box-sizing: border-box;
      border-radius: 4px;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      border: none;
      font-size: 28px;
    }

    .search h2 {
      text-align: center;
      color: #fff;
      -webkit-box-flex: 1;
      -webkit-flex: 1 0 100%;
      -ms-flex: 1 0 100%;
      flex: 1 0 100%;
      padding-bottom: 50px;
      font-size: 44px;
      font-family: "Noto Sans SC";
      font-weight: 100;
    }

    .search {
      min-width: 875px;
      -webkit-box-align: center;
      -webkit-align-items: center;
      -ms-flex-align: center;
      align-items: center;
      -webkit-flex-wrap: wrap;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap;
      position: absolute;
      top: 50%;
      z-index: 9;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      margin-top: -15px;
      -webkit-transition: color .5s;
      -o-transition: color .5s;
      transition: color .5s;
    }


  }

  .effect1 {
    min-height: 100vh;
    -webkit-animation: gradient 7.5s ease-in-out infinite;
    animation: gradient 7.5s ease-in-out infinite;
  }

  .effect1 {
    background: #0299d8;
    background: -webkit-linear-gradient(45deg, #5a3694, #13bdce 33%, #0094d9 66%, #6fc7b5);
    background: -o-linear-gradient(45deg, #5a3694 0, #13bdce 33%, #0094d9 66%, #6fc7b5 100%);
    background: linear-gradient(45deg, #5a3694, #13bdce 33%, #0094d9 66%, #6fc7b5);
    background-size: 400%;
    background-position: 0 100%;
  }

  @-webkit-keyframes gradient {
    50% {
      background-position: 100% 0
    }
  }

  @keyframes gradient {
    50% {
      background-position: 100% 0
    }
  }
</style>



