import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import search from '@/components/search'
import help from '@/components/help'
import tutorial from '@/components/regisertprocess'
import invite from '@/components/invite'
import list from '@/components/list'
import market from '@/components/market'
import marketx from '@/components/marketx'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/list',
      name: 'list',
      component: list
    },
    {
      path: '/search',
      name: 'search',
      component: search
    },
    {
      path: '/faq',
      name: 'help',
      component: help
    },
    {
      path: '/tutorial',
      name: 'tutorial',
      component: tutorial
    },
    {
      path: '/invite',
      name: 'invite',
      component: invite
    },
    {
      path: '/market',
      name: 'market',
      component: market
    },
    {
      path: '/marketx',
      name: 'marketx',
      component: marketx
    }
  ]
})



