<script>
  import { Loading } from 'element-ui'

  export default {
    name: 'market',
    data() {
      return {
        activeName: 'items',
        input4: '',
        list: [],
        listloading: true,
        options:
          [
            {
              value: '',
              label: 'Recently listed'
            }, {
            value: 'priceLow',
            label: 'Price: Low to High'
          }, {
            value: 'priceHigh',
            label: 'Price: High to Low'
          }],
        value: '',
        search: '',
        addr: '',
        hensname: '',
        currentPage: 1,
        key: '',
        sum: 0,
        _this: '',
        sellnum: 0,
        txt: '',
        txlisthash: [],
        txlisthashsum: 0,
        txlisthashcurrentPage: 1,
        price_usd: 0,
        txlisthashloading: true
      }
    },
    methods: {
      rowclick(row) {
        this.tomarketx(row.tokenid)
      },
      subname1(txt) {
        if (txt) {
          if (txt.length > 41) {
            return txt.substring(0, 6) + '...' + txt.substring(30, 42)
          } else {
            return txt
          }
        }
      },
      async sellfn() {
        this.$prompt('Please enter the ETC price to sell', '', {
          confirmButtonText: 'Ok',
          cancelButtonText: 'Cancel'
        }).then(async ({ value }) => {
          let loadingInstance = Loading.service({ fullscreen: true })
          let hash = await this.$g.offerHensForSale(this.txt, value, this.addr)
          if (hash != '') {
            let tt = await this.$g.eth_getTransactionReceipt(hash)
            loadingInstance.close()
            if (tt == '0x0') {
              this.status = 'error'
              this.$message.error('Error')
            } else {
              this.$message({
                message: 'Sell succeeded',
                type: 'success'
              })
            }
          } else {
            loadingInstance.close()
          }
        })
      },
      async cancelsellfn() {
        let loadingInstance = Loading.service({ fullscreen: true })
        let hash = await this.$g.hensNoLongerForSale(this.txt, this.addr)
        if (hash != '') {
          let tt = await this.$g.eth_getTransactionReceipt(hash)
          loadingInstance.close()
          if (tt == '0x0') {
            this.status = 'error'
            this.$message.error('Error')
          } else {
            this.$message({
              message: 'Cancel Sell succeeded',
              type: 'success'
            })
          }
        } else {
          loadingInstance.close()
        }
      },
      tomarketx(tokenid) {
        this.$router.push({ path: '/marketx', query: { 'tokenid': tokenid } })
      },
      selectchange() {
        this.currentPage = 1
        this.loadaxios()
      },
      txlisthashhandleSizeChange(val) {
        this.txlisthashcurrentPage = val
        this.txlisthashfn()
      },
      handleSizeChange(val) {
        this.currentPage = val
        this.loadaxios()
      },
      async invite() {
        this.$router.push({ path: '/invite' })
      },
      tutorial() {
        this.$router.push({ path: '/tutorial' })
      },
      handleCommand(command) {
        if (command == 'sellfn') {
          this.sellfn()
        }
        if (command == 'cancelsellfn') {
          this.cancelsellfn()
        }
        if (command == 'home') {
          window.location = './'
        }
        if (command == 'my') {
          this.my()
        }
        if (command == 'faq') {
          this.faq()
        }
        if (command == 'tutorial') {
          this.tutorial()
        }
        if (command == 'listfn') {
          this.listfn()
        }
        if (command == 'withdraw') {
          this.$g.openwithdraw(this._this)
        }

      },
      async listfn() {
        this.$router.push({ path: '/list' })
      },
      async my() {
        if (this.$route.path != '/search') {
          this.$router.push({ path: '/search', query: { 'address': this.addr } })
        }
      },
      faq() {
        this.$router.push({ path: '/faq' })
      },
      subname(txt) {
        if (this.hensname != '') {
          return this.hensname
        }
        if (txt) {
          if (txt.length > 41) {
            return txt.substring(0, 6) + '...' + txt.substring(30, 42)
          } else {
            return txt
          }
        }
      },
      async searchfn() {
        this.currentPage = 1
        this.loadaxios()
      },
      async loginout() {
        this.addr = ''
      },
      async login() {
        if (typeof (ethereum) !== 'undefined') {
          this.addr = await this.$g.etclogin()
          if (this.addr != '') {
            this.hensname = await this.$g.getNameOfOwner(this.addr)
            this.txt = this.hensname.substring(0, (this.hensname.length - 4))
            let tokenid = await this.$g.getTokenIdOfName(this.txt)
            this.sellnum = await this.$g.henssOfferedForSale(tokenid)
          }
        }
        this.$axios.get('https://apis.etcswap.cc/?quotesTxt=etc').then(res => {
          this.price_usd = res.data[0].ticker.price_usd
        })
      },
      loadaxios() {
        this.listloading = true
        this.$axios.post('https://json.hens.domains/txlist', {
          // this.$axios.post('http://127.0.0.1:3666/txlist', {
          'key': this.key,
          'num': this.currentPage,
          'type': this.value
        }).then(async res => {
          this.listloading = false

          this.list = []
          this.sum = res.data.count
          res.data.list.forEach(item => {
            item.img = 'https://json.hens.domains/hens/' + item.tokenid + '/hens.jpg'
            this.list.push(item)
          })
        })
      },
      txlisthashfn() {
        this.txlisthashloading = true
        this.$axios.post('https://json.hens.domains/txlisthash', {
          'tokenid': '',
          'num': this.txlisthashcurrentPage,
          'type': 'buy'
        }).then(async res => {
          this.txlisthashloading = false
          this.txlisthash = []
          this.txlisthashsum = res.data.count
          res.data.list.forEach(item => {
            item.img = 'https://json.hens.domains/hens/' + item.tokenid + '/hens.jpg'
            this.txlisthash.push(item)
          })
        })
      }
    },
    mounted() {
      this.loadaxios()
      this.login()
      this.txlisthashfn()
      this._this = this
    }
  }
</script>

