  async getRenewalPriceByNameAndDu(name, year) {
    console.log(name, 'getRenewalPriceByNameAndDu')

    return new Promise(async (resolve, reject) => {
      let web3 = new Web3()
      let getPriceByNameAndDuabi = web3.eth.abi.encodeFunctionCall({
        name: 'getRenewalPriceByNameAndDu',
        type: 'function',
        inputs: [{
          type: 'string',
          name: 'name_'
        }, {
          type: 'uint256',
          name: 'durationYear'
        }]
      }, [name, year])
      ethereum.request({
        'method': 'eth_call',
        'params': [{
          'to': Hsn,
          'data': getPriceByNameAndDuabi
        }, 'latest']
      }).then(async (res) => {
        let price = web3Utils.hexToNumberString(res)
        let ret = new BigNumber(price)
        price = parseFloat((parseFloat(ret.dividedBy(Ether)) + 0.000001).toFixed(6))
        resolve(price)
      })
        .catch((err) => {
          resolve('')
        })
    })
  },
