  async mint(addr, name, year, price, invite) {
    return new Promise(async (resolve, reject) => {
      let web3 = new Web3()
      let mintabi = web3.eth.abi.encodeFunctionCall({
        name: 'mint',
        type: 'function',
        inputs: [{
          type: 'string',
          name: 'name_'
        },
          {
            type: 'uint256',
            name: 'durationYear'
          },
          {
            type: 'string',
            name: 'invite'
          }
        ]
      }, [name, year, invite])
      const transactionParameters = {
        gasPrice: web3Utils.toHex((gas * 10e+8)),
        gas: web3Utils.toHex(gasLimit),
        to: Hsn,
        from: addr,
        value: web3Utils.toHex(parseInt(price * 10e+17)),
        data: mintabi,
        chainId: network.chainId
      }

      const txHash = await ethereum.request({
        method: 'eth_sendTransaction',
        params: [transactionParameters]
      })
      resolve(txHash)
    })
  },
