  getsplitname(txt) {
    let text = JSON.stringify(txt)
    text = text.split('\\u0000')[text.split('\\u0000').length - 1]
    if (text.indexOf('\\b') != -1) {
      text = text.split('\\b')[1]
    }
    if (text.indexOf('\\u000b') != -1) {
      text = text.split('\\u000b')[1]
    }
    if (text.indexOf('\\u000') != -1) {
      text = text.split('\\u000')[1]
      text = text.substring(1)
    }
    if (text.indexOf('"') != -1) {
      text = text.split('"')[0]
    }
    return text
  },
