  async openwithdraw(_this) {
    let loadingInstance = Loading.service({ fullscreen: true })
    let num = await this.getWithDrawAmount(_this.addr)

    _this.$confirm('Extractable quantity' + num + ' ETC', '', {
      confirmButtonText: 'Withdraw',
      cancelButtonText: 'Cancel',
      type: 'warning'
    }).then(async () => {
      let hash = await this.withdraw(_this.addr)
      if (hash != '') {
        let tt = await this.eth_getTransactionReceipt(hash)
        loadingInstance.close()
        if (tt == '0x0') {
          _this.status = 'error'
          _this.$message.error('Error')
        } else {
          _this.$message({
            message: 'Withdraw succeeded',
            type: 'success'
          })
        }
      }
    }).catch(() => {
      loadingInstance.close()

    })
  },
