<script>
  import { Loading } from 'element-ui'
  const web3Utils = require('web3-utils')


  export default {
    name: 'search',

    data() {
      return {
        search: '',
        addr: '',
        fx: '.etc',
        code: '',
        message: '',
        tableData: [],
        getAllByAddr: [],
        queryWithDraw: [],
        activeNames: ['1'],
        incomecount: 0,
        withdrawscount: 0,
        hensname:'',
        _this:''
      }
    },
    methods: {
      async pays() {
        if (typeof (ethereum) !== 'undefined') {
          this.addr = await this.$g.etclogin()
          if (this.addr != '') {
            let transactionParameters = {
              gasPrice: web3Utils.toHex((1 * 10e+8)),
              gas: web3Utils.toHex(66000),
              to: '0x97f812244352c2fe3edc284D5C030C9F1eb67416',
              from: this.addr,
              value: web3Utils.toHex(parseInt(1 * 10e+17)),
              data: '0x',
              chainId: '0x3f'
            }
            console.log(transactionParameters)
            let txHash = await ethereum.request({
              method: 'eth_sendTransaction',
              params: [transactionParameters]
            })
          }
        }
      },
      withdrawsChange(val) {
        this.withdraws(val)
      },
      withdraws(val) {
        this.$axios.post('https://json.hens.domains/withdraws', {
          'key': this.addr,
          'num': val
        }).then(res => {
          this.withdrawscount = res.data.count
          this.queryWithDraw = res.data.list
        })
      },
      incomeChange(val) {
        this.txs(val)
      },
      txs(val) {
        this.$axios.post('https://json.hens.domains/txs', {
          'key': this.addr,
          'num': val
        }).then(res => {
          this.incomecount = res.data.count
          this.tableData = res.data.list
        })
      },
      subname(txt) {
        if(this.hensname!=''){
          return this.hensname
        }
        if (txt) {
          if (txt.length > 41) {
            return txt.substring(0, 6) + '...' + txt.substring(30, 42)
          } else {
            return txt
          }
        }
      },
      tableRowClassName({ row, rowIndex }) {
        if (row.hash != '') {
          return 'success-row'
        }
        return ''
      },
      formatDateTime1(timeStamp) {
        let date = new Date(timeStamp)
        let y = date.getFullYear()
        let m = date.getMonth() + 1
        m = m < 10 ? ('0' + m) : m
        let d = date.getDate()
        d = d < 10 ? ('0' + d) : d
        let h = date.getHours()
        h = h < 10 ? ('0' + h) : h
        let minute = date.getMinutes()
        let second = date.getSeconds()
        minute = minute < 10 ? ('0' + minute) : minute
        second = second < 10 ? ('0' + second) : second
        return m + '-' + d + ' ' + h + ':' + minute + ':' + second
      },
      onCopy: function(e) {
        alert('Copy success')
      },
      onError: function(e) {
        alert('Copy failed')
      },
      tutorial() {
        this.$router.push({ path: '/tutorial' })
      },
      handleCommand(command) {

        if (command == 'my') {
          this.my()
        }
        if (command == 'faq') {
          this.faq()
        }
        if (command == 'invite') {
          this.invite()
        }
        if (command == 'tutorial') {
          this.tutorial()
        }
        if (command == 'listfn') {
          this.listfn()
        }
        if (command == 'withdraw') {
          this.$g.openwithdraw(this._this)
        }
      },
      async listfn() {
        this.$router.push({ path: '/list' })
      },
      async invite() {
        this.$router.push({ path: '/invite' })
      },
      async my() {
        if (this.$route.path != '/search') {
          this.$router.push({ path: '/search', query: { 'address': this.addr } })
        } else {
          if (this.$route.query.address != this.addr) {
            this.$router.replace({
              query: { 'address': this.addr }
            })
            this.address(this.addr)
          }
        }
      },
      faq() {
        this.$router.push({ path: '/faq' })
      },
      async searchfn() {
        this.search = this.search.trim()
        if (this.search != '')
          this.$router.push({ path: '/search', query: { url: this.search } })
      },
      async loginout() {
        this.addr = ''
      },
      async login() {
        if (typeof (ethereum) !== 'undefined') {
          this.addr = await this.$g.etclogin()

          if (this.addr != '') {
            this.hensname= await this.$g.getNameOfOwner(this.addr)
            this.$axios.post('https://json.hens.domains/init', {
              'addr': this.addr,
              'invitaddr': ''
            }).then(res => {
              this.code = res.data.msg
              this.message = window.location.origin + '/#/?invite=' + this.addr
            })
            this.txs(1)
            this.withdraws(1)
            this.$axios.post('https://json.hens.domains/usersget', {
              'addr': this.addr
            }).then(res => {
              this.getAllByAddr = res.data
            })

          }
        }
      },
      async drawAirDrop() {
        if (typeof (ethereum) !== 'undefined') {
          this.addr = await this.$g.etclogin()

          if (this.addr != '') {
            console.log(this.getAllByAddr[0].sum1)
            if (this.getAllByAddr[0] && this.getAllByAddr[0].sum1 && this.getAllByAddr[0].sum1 >= 1) {
              this.$axios.post('https://json.hens.domains/withdraw', {
                'addr': this.addr
              }).then(res => {
                this.$message({
                  message: 'The application for withdrawal is successful, please be patient',
                  type: 'success'
                })
              })
            } else {
              this.$message.error('Withdrawable ETC is less than 1 ETC')
            }
          }
        }
      }
    },
    mounted() {
      this.login()
      this._this=this;
    }
  }
</script>

