      color: #444;
      font-size: 20px;
      line-height: 50px;
      font-weight: 400;
    }

  }

  .title {
    float: left;
    font-size: 16px;
    margin-left: 8px;
    font-family: "Noto Sans SC";
    font-weight: 300;
  }

  .head .headlist {
    float: right;
    font-size: 14px;
    margin-right: 20px;

    .active {
      border-bottom: 1px solid #333435;
    }

    a {
      display: block;
      color: #333435;
      float: left;
      padding: 0 20px;
      -webkit-touch-callout: none;
      text-decoration: none;
      cursor: pointer;
      -moz-user-select: none;
      -webkit-user-select: none;
      -ms-user-select: none;
      user-select: none;

    }

    a:hover {
      color: #909090
    }
  }

  .head .btn {
    font-size: 14px;
    border: 2px solid #00a8ff;
    text-align: center;
    margin-top: 10px;
    height: 36px;
    line-height: 36px;
    border-radius: 42px;
    width: 120px;
    float: right;
    cursor: pointer;
  }

  .head .btn:hover {
    background-color: #00a8ff;
    color: #fff;
  }

  .head .content {
    width: 96%;
    margin: 0 auto;
    max-width: 1800px;
  }

  .head {
    line-height: 60px;
    height: 60px;
    width: 100%;
    background: hsla(0, 0%, 100%, 0.7);
    color: #333435;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    border-bottom: 1px solid #e5e5e5;
  }
</style>



