<script>
  export default {
    name: 'regisertprocess',
    data() {
      return {
        activeName: 'registration',
        search: '',
        addr: '',
        fx: '.etc',
        hensname: '',
        _this:''
      }
    },
    methods: {
      handleCommand(command) {
        if (command == 'home') {
          window.location = './'
        }
        if (command == 'my') {
          this.my()
        }
        if (command == 'faq') {
          this.faq()
        }
        if (command == 'invite') {
          this.invite()
        }

        if (command == 'listfn') {
          this.listfn()
        }
        if (command == 'withdraw') {
          this.$g.openwithdraw(this._this)
        }
      },
      async listfn() {
        this.$router.push({ path: '/list' })
      },
      async invite() {
        this.$router.push({ path: '/invite' })
      },
      async my() {
        if (this.$route.path != '/search') {
          this.$router.push({ path: '/search', query: { 'address': this.addr } })
        } else {
          if (this.$route.query.address != this.addr) {
            this.$router.replace({
              query: { 'address': this.addr }
            })
            this.address(this.addr)
          }
        }
      },
      faq() {
        this.$router.push({ path: '/faq' })
      },
      subname(txt) {
        if (this.hensname != '') {
          return this.hensname
        }
        if (txt) {
          if (txt.length > 41) {
            return txt.substring(0, 6) + '...' + txt.substring(30, 42)
          } else {
            return txt
          }
        }
      },
      async searchfn() {
        this.search = this.search.trim()
        if (this.search != '')
          this.$router.push({ path: '/search', query: { url: this.search } })
      },
      async loginout() {
        this.addr = ''
      },
      async login() {
        if (typeof (ethereum) !== 'undefined') {
          this.addr = await this.$g.etclogin()
          if (this.addr != '') {
            this.balance = await this.$g.getBalance(this.addr)
            this.hensname = await this.$g.getNameOfOwner(this.addr)
          }
        }
      }
    },
    mounted() {
      this.login()
      this._this=this;
    }
  }
</script>

