    height: 36px;
    line-height: 36px;
    border-radius: 42px;
    width: 120px;
    float: right;
    cursor: pointer;
  }

  .head .btn:hover {
    background-color: #00a8ff;
    color: #fff;
  }

  .head .content {
    width: 96%;
    margin: 0 auto;
    max-width: 1800px;
  }

  .head {
    line-height: 60px;
    height: 60px;
    width: 100%;
    background: hsla(0, 0%, 100%, 0.7);
    color: #333435;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    border-bottom: 1px solid #e5e5e5;
  }
</style>




