<template>
  <div id="app">
    <router-view/>

  </div>
</template>

<script>
  export default {
    name: 'App',
    methods: {
      subname(txt) {
        if (txt) {
          if (txt.length > 41) {
            return txt.substring(0, 12) + '...' + txt.substring(30, 42)
          } else {
            return txt
          }
        }
      },
      load(){
        setTimeout(()=>{
          this.load()
        },10000)
        this.$axios.post('https://json.hens.domains/txmint', {
          'key': '',
          'num': 1
        }).then(res => {
          let sum = Math.ceil(Math.random() * res.data.list.length)
          if (res.data.list[sum]) {
            this.$notify({
              title: "Mint"+res.data.list[sum].name,
              message: this.subname(res.data.list[sum].addr),
              position: 'bottom-left',
            })
          }
        })
      }
    },
    mounted() {
      this.load()
    }
  }
</script>

<style>
  .el-message-box {
    width: 320px !important;
  }
  .el-dropdown {
    display: none !important;
  }

  .showmemu {
    display: none;
  }

  .showmemu1 {
    display: block;
  }

  .showmemu1 div:hover {
    cursor: pointer;
  }

  @media (max-width: 768px) {

    .showmemu {
      display: block !important;
    }

    .showmemu1 {
      display: none !important;
    }
  }

  /*  */
  html {
    color: #000;
    background: rgb(240, 246, 250);;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    font-size: 100%;
  }

  /*  */
  body, div, dl, dt, dd, ul, li, pre, form, fieldset, select, input, textarea, button, p, blockquote, th, td, img, iframe {
    margin: 0;
    padding: 0;
  }

  body {
    width: 100%;
    font-family: Overpass;
  }

  /*  font  */
  body, button, input, select, textarea {
    color: #333435;
    outline: none;
  }

  input, select, textarea {
    font-size: 100%;
  }

  /* button */
  button {
    border: none;
  }

  /* Table cell  */
  table {
    border-collapse: collapse;
    border-spacing: 0;
  }

  /* IE bug fixed: th  text-align*/
  th {
    text-align: inherit;
  }

  /* ,  */
  caption, th {
    text-align: left;
  }

  /*  */
  fieldset, img {
    border: none;
    outline: none;
    -moz-outline: none;
  }

  /* ie6 7 8(q) bug  */
  iframe {
    display: block;
  }

  /* , li  */
  ol, ul, li {
    list-style: none;
  }

  /* yahoo, ,  */
  h1, h2, h3, h4, h5, h6 {
    font-size: 100%;
    font-weight: 500;
    margin: 0;
    padding: 0;
  }

  /* hover */
  a {
    color: #333;
    cursor: pointer;
    outline: none;
    text-decoration: none;
    blr: expression(this.onFocus=this.blur());
  }

  a:hover {
    text-decoration: underline;
  }

  /*  */
  .clearfix:before, .clearfix:after {
    display: block;
    content: " ";
    clear: both;
  }

  .clearfix {
    zoom: 1; /* for ie67*/
  }

  /*  HTML5 , IE  js  createElement(TAG) */
  article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
    display: block;
  }

  /* HTML5  img  */
  audio, canvas, video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
  }

  address, caption, cite, code, dfn, em, th {
    font-style: normal;
    font-weight: normal;
  }

  /*css3FORM*/
  .box-sizing {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -o-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
  }
</style>



