                    </el-col>
                    <el-col :xs="24" :sm="10">
                      <span style="visibility: hidden" v-if="!addrtxt&&item.addr==''">{{index}}</span>
                      <span v-if="!addrtxt" style="word-break: break-all;">{{item.addr}}</span>
                      <span v-if="addrtxt"><el-input v-model="item.addr"
