                    <el-input-number v-model="year" @change="handleChange1" :min="1" :max="100000"
                                     label="Year"></el-input-number>
                  </div>
                  <div style="float: left;margin-left: 10px;margin-top: 4px">year</div>
                </div>
                <div style="font-size: 14px; color: rgb(173, 187, 205);margin-top: 10px;">
                  Registration Period
                </div>
              </div>
              <div class="icon-right" style="float: left;margin-left: 10px;margin-top: 8px">
                <i style="color: rgb(173, 187, 205)" class="el-icon-right"></i>
              </div>
              <div style="float: left">
                <div style="overflow: hidden">
                  <div class="mleft" style="margin-left: 10px;float: left;width: 180px">
                    <el-input class="input_el_input__inner" readonly v-model="price">
                      <template slot="append">ETC</template>
                    </el-input>
