                  Registration price to pay
                </div>
              </div>
            </div>

            <div class="extends1">
              {{price}} ETC + at most 0.0008 ETC gas fee = at most {{(price+0.0008).toFixed(6)}} ETC
              <span
                style="color: rgb(173, 187, 205);margin-left: 20px">${{((price+0.0008)*price_usd).toFixed(4)}}USD</span>
            </div>
            <div style="font-size: 14px;
    color: rgb(173, 187, 205);
    margin-top: 10px;">Estimated Total (Price + Gas). The gas price is based at 1 Gwei
            </div>

            <div style="color: rgb(246, 65, 45);margin-top: 10px">
