        </div>
      </div>
      <div style="height: 40px"></div>
    </div>
  </div>
</template>

<script>
  import { Loading } from 'element-ui'

  const web3Utils = require('web3-utils')

  export default {
    name: 'search',
    data() {
      return {
        ispc: true,
        addr: '',
        search: '',
        txt: '',
        year: 1,
        searchis: -1,
        register: false,
        details: false,
        fx: '.etc',
        price: 0,
        price_usd: 0,
        balance: 0,
        height: 0,
        extend: false,
        detailsmodel: {
          addr: '',
          start: 0,
          end: 0,
          last: 0
        },
        owner: {
          addr: '',
          name: '',
          start: 0,
          end: 0,
          last: 0
        },
        addressis: false,
        textlist: [
          {
            txt: 'Url',
            get: 'getUrl',
            set: 'setUrl',
            val: ''
          }, {
            txt: 'Telegram',
            get: 'getOrgTelegram',
            set: 'setOrgTelegram',
            val: ''
          }, {
            txt: 'Notice',
            get: 'getNotice',
            set: 'setNotice',
            val: ''
          }, {
            txt: 'Keywords',
            get: 'getKeywords',
            set: 'setKeywords',
            val: ''
          }, {
            txt: 'IpfsUrl',
            get: 'getIpfsUrl',
            set: 'setIpfsUrl',
            val: ''
          }, {
            txt: 'Email',
            get: 'getEmail',
            set: 'setEmail',
            val: ''
          }, {
            txt: 'Description',
            get: 'getDescription',
            set: 'setDescription',
            val: ''
          }, {
            txt: 'Twitter',
            get: 'getComTwitter',
            set: 'setComTwitter',
            val: ''
          }, {
            txt: 'Reddit',
            get: 'getComReddit',
            set: 'setComReddit',
            val: ''
          }, {
            txt: 'Github',
            get: 'getComGithub',
            set: 'setComGithub',
            val: ''
          }, {
            txt: 'Avatar',
            get: 'getAvatar',
            set: 'setAvatar',
            val: ''
          }
        ],
        ipfslist: [
          {
            txt: 'AddressIpfs',
            get: 'getAddressIpfs',
            set: 'setAddressIpfs'
          }
        ],
        coin: [
          'Etc', 'Btc', 'Eth', 'Xrp', 'Bch', 'Bsv', 'Ltc', 'Trx', 'Xlm', 'Doge', 'Btg', 'Nxt',
          'Ardor', 'Rvn', 'Lsk', 'Kmd'
        ],
        edittxt: false,
        addrtxt: false,
        listcoin: [],
        registersum: 0,
        status: 'success',
        textloading: false,
        addrloading: false,
        extendsaveloading: false,
        transferloading: false,
        hensname: '',
        sellloading: false,
        tokenid: 0,
        sellnum: 0,
        cancelsellloading: false,
        buyloading: false,
        _this: '',
        img: ''
      }
    },
    methods: {
      market() {
        this.$router.push({ path: '/market' })
      },
      async buyfn() {
        let hash = await this.$g.buyHens(this.txt, this.sellnum, this.addr)

        if (hash != '') {
          this.buyloading = true
          let tt = await this.$g.eth_getTransactionReceipt(hash)
          this.buyloading = false
          if (tt == '0x0') {
            this.status = 'error'
            this.$message.error('Error')
          } else {
            this.$message({
              message: 'Buy succeeded',
              type: 'success'
            })
            this.detailsfn()
          }
        }
      },
      async cancelsellfn() {
        let hash = await this.$g.hensNoLongerForSale(this.txt, this.addr)
        if (hash != '') {
          this.cancelsellloading = true
          let tt = await this.$g.eth_getTransactionReceipt(hash)
          this.cancelsellloading = false
          if (tt == '0x0') {
            this.status = 'error'
            this.$message.error('Error')
          } else {
            this.$message({
              message: 'Cancel Sell succeeded',
              type: 'success'
            })
            this.detailsfn()
          }
        }
      },
      async sellfn() {
        this.$prompt('Please enter the ETC price to sell', '', {
          confirmButtonText: 'Ok',
          cancelButtonText: 'Cancel'
        }).then(async ({ value }) => {
          let hash = await this.$g.offerHensForSale(this.txt, value, this.addr)
          console.log(hash)
          if (hash != '') {
            this.sellloading = true
            let tt = await this.$g.eth_getTransactionReceipt(hash)
            this.sellloading = false
            if (tt == '0x0') {
              this.status = 'error'
              this.$message.error('Error')
            } else {
              this.$message({
                message: 'Sell succeeded',
                type: 'success'
              })
              this.detailsfn()
            }
          }
        })
      },
      async transferfn() {
        this.$confirm('This operation will transfer ' + this.search + this.fx + ' to others permanently. Do you want to continue?', '', {
          confirmButtonText: 'Ok',
          cancelButtonText: 'Cancel',
          type: 'warning'
        }).then(async () => {
          this.$prompt('Please enter Etc address', '', {
            confirmButtonText: 'Ok',
            cancelButtonText: 'Cancel'
          }).then(async ({ value }) => {
            if (web3Utils.isAddress(value)) {
              this.transferloading = true

              let name = await this.$g.getNameOfOwner(value)
              if (name == '') {
                let hash = await this.$g.transferName(this.addr, value, this.txt)
                if (hash != '') {
                  let tt = await this.$g.eth_getTransactionReceipt(hash)
                  if (tt == '0x0') {
                    this.status = 'error'
                    this.$message.error('Error')
                  } else {
                    this.$message({
                      message: 'Extend succeeded',
                      type: 'success'
                    })
                    this.detailsfn()
                  }
                }
              } else {
                this.$message.error('ErrorThis address already has a HENS name')
              }
              this.transferloading = false
            } else {
              this.$message.error('Address Error')
            }
          }).catch(() => {

          })
        }).catch(() => {

        })
      },
      tutorial() {
        this.$router.push({ path: '/tutorial' })
      },
      handleCommand(command) {
        if (command == 'home') {
          window.location = './'
        }
        if (command == 'my') {
          this.my()
        }
        if (command == 'faq') {
          this.faq()
        }
        if (command == 'invite') {
          this.invite()
        }
        if (command == 'tutorial') {
          this.tutorial()
        }
        if (command == 'listfn') {
          this.listfn()
        }
        if (command == 'withdraw') {
          this.$g.openwithdraw(this._this)
        }
        if (command == 'market') {
          this.market()
        }

      },
      async listfn() {
        this.$router.push({ path: '/list' })
      },
      async invite() {
        this.$router.push({ path: '/invite' })
      },
      async my() {
        if (this.$route.path != '/search') {
          this.$router.push({ path: '/search', query: { 'address': this.addr } })
        } else {
          if (this.$route.query.address != this.addr) {
            this.$router.replace({
              query: { 'address': this.addr }
            })
            this.address(this.addr)
          }
        }
      },
      faq() {
        this.$router.push({ path: '/faq' })
      },
      async saveaddr() {
        this.addrloading = true
        let list = []
        this.listcoin.forEach(item => {
          if (item.val != '' && item.addr != '') {
            list.push({
              val: item.val.trim(),
              addr: item.addr.trim()
            })
          }
        })
        let res = await this.$axios.post('https://apis.hebe.cc/ipfs/', {
          json: JSON.stringify(list)
        })
        let txt = ''
        this.textlist.forEach(item => {
          txt = txt + web3Utils.toHex(item.val) + '+ '
        })
        txt = txt + res.data
        let hash = await this.$g.setAllProperties(txt, this.txt + this.fx, this.addr)
        if (hash != '') {
          let tt = await this.$g.eth_getTransactionReceipt(hash)
          this.addrloading = false
        }
        this.addrtxt = false
      },
      delcoin(index) {
        this.listcoin.splice(index, 1)
      },
      addcoin() {
        if (this.listcoin.length == 0) {
          this.listcoin.push({
            val: 'Etc',
            addr: this.addr
          })
        } else {
          this.listcoin.push({
            val: '',
            addr: ''
          })
        }
      },
      async savetext() {
        this.textloading = true
        let txt = ''
        this.textlist.forEach(item => {
          txt = txt + web3Utils.toHex(item.val) + '+ '
        })
        let hash = await this.$g.setAllProperties(txt, this.txt + this.fx, this.addr)
        if (hash != '') {
          let tt = await this.$g.eth_getTransactionReceipt(hash)
          this.textloading = false
        }
        this.edittxt = false

      },
      settext() {
        this.edittxt = true

      },
      async extendsave() {
        this.search = this.search.trim()
        this.extendsaveloading = true
        let hash = await this.$g.renewal(this.addr, this.txt, this.year, this.price)
        if (hash != '') {
          let tt = await this.$g.eth_getTransactionReceipt(hash)
          this.extendsaveloading = false
          if (tt == '0x0') {
            this.status = 'error'
            this.$message.error('Error')
          } else {
            this.$message({
              message: 'Extend succeeded',
              type: 'success'
            })
            this.detailsfn()
          }
        }
      },
      async extendfn() {
        this.details = false
        let loadingInstance = Loading.service({ fullscreen: true })
        this.price = await this.$g.getRenewalPriceByNameAndDu(this.txt, this.year)
        this.extend = true
        loadingInstance.close()
      },
      async detailsfn() {
        this.register = false
        this.details = false
        this.extend = false
        this.searchis = -1
        this.tokenid = await this.$g.getTokenIdOfName(this.txt)
        this.sellnum = await this.$g.henssOfferedForSale(this.tokenid)
        let loadingInstance = Loading.service({ fullscreen: true })
        this.detailsmodel.start = await this.$g.getStartBlock(this.txt + this.fx)
        this.detailsmodel.addr = await this.$g.getowner(this.txt + this.fx)
        let is = await this.$g.isOverLine(this.txt + this.fx)
        let txt = await this.$g.getAllProperties(this.txt + this.fx)
        if (txt != '' && txt.split('+').length > 10) {
          this.textlist.forEach((item, index) => {
            item.val = txt.split('+')[index].trim()
            try {
              item.val = web3Utils.hexToString(item.val)
            } catch (e) {
              try {
                item.val = web3Utils.hexToNumber(item.val)
              } catch (e) {
                item.val = item.val
              }
            }

          })
          if (txt.split('+')[11] != '') {
            let addripfs = txt.split('+')[11].trim()
            console.log(addripfs, 'addripfs')
            // addripfs=web3Utils.hexToString(addripfs)

            if (addripfs != '' && addripfs.length > 10) {
              let ress = await this.$axios.get('https://apis.hebe.cc/ipfs/?id=' + addripfs)
              this.listcoin = ress.data
            }
          }
        }
        this.height = parseFloat(is.isOverLine) - parseFloat(is.blockNumber)
        this.detailsmodel.end = is.isOverLine
        this.detailsmodel.last = this.height
        this.details = true
        loadingInstance.close()
      },
      async registerfn() {
        this.searchis = -1
        let loadingInstance = Loading.service({ fullscreen: true })
        this.price = await this.$g.getPriceByNameAndDu(this.txt, this.year)
        loadingInstance.close()
        this.register = true
      },
      async handleChange1() {
        let loadingInstance = Loading.service({ fullscreen: true })
        this.price = await this.$g.getRenewalPriceByNameAndDu(this.search, this.year)
        loadingInstance.close()
      },
      async handleChange() {
        let loadingInstance = Loading.service({ fullscreen: true })
        this.price = await this.$g.getPriceByNameAndDu(this.txt, this.year)
        loadingInstance.close()
      },
      async searchfn() {
        if (this.search != '') {
          this.status = 'success'
          this.registersum = 0
          this.register = false
          this.details = false
          this.search = this.search.trim().toLowerCase()
          this.txt = this.search
          this.extend = false
          this.addressis = false
          if (this.$route.query.url != this.txt) {
            this.$router.replace({
              query: { 'url': this.txt }
            })
          }
          let loadingInstance = Loading.service({ fullscreen: true })
          let is = await this.$g.isOverLine(this.txt + this.fx)
          loadingInstance.close()
          if (parseFloat(is.isOverLine) < parseFloat(is.blockNumber)) {
            this.searchis = 0
          } else {
            this.searchis = 1
            this.height = parseFloat(is.isOverLine) - parseFloat(is.blockNumber)
            this.detailsmodel.end = is.isOverLine
            this.detailsmodel.last = this.height
          }
        }
      },
      async loginout() {
        this.addr = ''
      },
      async login() {
        if (typeof (ethereum) !== 'undefined') {
          this.addr = await this.$g.etclogin()
          if (this.addr != '') {
            // this.$g.buyHens('1',0.02,this.addr)

            this.balance = await this.$g.getBalance(this.addr)
            this.hensname = await this.$g.getNameOfOwner(this.addr)
            if (this.$g.invite != '') {
              this.$axios.post('https://json.hens.domains/init', {
                'addr': this.addr,
                'invitaddr': this.$g.invite
              }).then(res => {
                console.log(res)
              })
            }
          }
        }
      },
      subname(txt) {
        if (this.hensname != '') {
          return this.hensname
        }
        if (txt) {
          if (txt.length > 41) {
            return txt.substring(0, 6) + '...' + txt.substring(30, 42)
          } else {
            return txt
          }
        }
      },
      etcquotes() {
        setTimeout(() => {
          this.etcquotes()
        }, 10000)
        this.$axios.get('https://apis.etcswap.cc/?quotesTxt=etc').then(res => {
          this.price_usd = res.data[0].ticker.price_usd
        })
      },
      async save() {
        this.registersum = 1
        this.owner.name = await this.$g.getNameOfOwner(this.addr)
        let is = await this.$g.isOverLine(this.owner.name)
        this.height = is.isOverLine - is.blockNumber
        this.owner.end = is.isOverLine
        this.owner.last = this.height
        this.owner.start = await this.$g.getStartBlock(this.owner.name)
        if (this.owner.last > 0) {
          this.$message({
            showClose: true,
            message: 'Only one hens name can be registered per address',
            type: 'error'
          })
          this.registersum = -1
          return
        }
        let hash = await this.$g.mint(this.addr, this.txt, this.year, this.price, this.$g.invite)
        this.registersum = 2
        if (hash != '') {
          let tt = await this.$g.eth_getTransactionReceipt(hash)
          this.hensname = await this.$g.getNameOfOwner(this.addr)
          if (tt == '0x1') {
            this.registersum = 3
            this.register = false
            this.address(this.addr)
          } else if (tt == '0x0') {
            this.status = 'error'
            this.registersum = 3
          }
        }
      },
      async address(addr) {
        this.register = false
        this.details = false
        this.extend = false
        this.searchis = -1

        let loadingInstance = Loading.service({ fullscreen: true })
        this.owner.addr = addr
        this.owner.name = await this.$g.getNameOfOwner(addr)
        let is = await this.$g.isOverLine(this.owner.name)
        this.height = is.isOverLine - is.blockNumber
        this.owner.end = is.isOverLine
        this.owner.last = this.height
        this.owner.start = await this.$g.getStartBlock(this.owner.name)
        this.addressis = true
        let name = this.owner.name
        if (name.indexOf('.etc') != -1) {
          name = name.slice(0, name.length - 4)
        }
        this.img = await this.$g.getTokenIdOfName(name)
        loadingInstance.close()
      },
      nameclick(name) {
        if (name.indexOf(this.fx) != -1) {
          name = name.split(this.fx)[0]
        }

        this.addressis = false

        this.$router.replace({
          query: { 'url': name }
        })
        this.txt = name
        this.search = name
        this.detailsfn()
      }
    },
    async mounted() {

      if (document.body.clientWidth > 960) {
        this.ispc = true
      } else {
        this.ispc = false
      }
      this.login()
      this.etcquotes()
      this._this = this
      if (this.$route.query && this.$route.query.url && this.$route.query.url != '') {
        this.search = this.$route.query.url
        this.txt = this.search
        this.searchfn()
      }
      if (this.$route.query && this.$route.query.address && this.$route.query.address != '') {
        this.address(this.$route.query.address)
      }
      if (this.$route.query && this.$route.query.addressurl && this.$route.query.addressurl != '') {
        this.txt = this.$route.query.addressurl
        this.detailsfn()
      }
    }
  }
</script>

