          -webkit-align-items: center;
          -ms-flex-align: center;
          align-items: center;
          padding: 0 10px;
          -webkit-touch-callout: none;
          text-decoration: none;
          cursor: pointer;
          -moz-user-select: none;
          -webkit-user-select: none;
          -ms-user-select: none;
          user-select: none;

          img {
            width: 50px;
            border-radius: 50px;
          }

          .text {
            padding-left: 10px;

            h4 {
              color: #444;
              font-size: 16px;
              font-weight: 400;

              span {
                font-size: 12px;
                color: #9c9c9c;
                background: #fff;
                padding: 2px 4px;
                border-radius: 2px;
                margin: 0 5px;
              }
            }

            p {
              color: #999;
              line-height: 24px;
              font-size: 14px;
              height: 24px;
              overflow: hidden;
            }
          }
        }
      }
    }

    max-width: 1180px;
    margin: 60px auto;
    padding-top: 20px;

    .topsearch {
      background: #fff;
      border-radius: 10px;
      overflow: hidden;
      height: 68px;
      line-height: 68px;

      .searchBtn {
        width: 20%;
        height: 60px;
        float: left;
        border-radius: 4px;
        border-top-left-radius: 0px;
        border-bottom-left-radius: 0px;
        background: #409EFF;
        border: #00a8ff;
        color: #282828;
        cursor: pointer;
      }

      .searchInput {
        float: left;
        width: 80%;
        height: 60px;
        text-indent: 15px;
        background: #fff !important;
        color: #444 !important;
        transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
        line-height: 40px;
        outline: none;
        padding: 0 15px;
        display: inline-block;
        box-sizing: border-box;
        border-radius: 4px;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        border: none;
        font-size: 24px;
      }

      .search h2 {
        text-align: center;
        color: #fff;
        -webkit-box-flex: 1;
        -webkit-flex: 1 0 100%;
        -ms-flex: 1 0 100%;
        flex: 1 0 100%;
        padding-bottom: 50px;
        font-size: 44px;
        font-family: "Noto Sans SC";
        font-weight: 100;
      }

      .search {
        -webkit-box-align: center;
        -webkit-align-items: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        z-index: 9;

        margin-top: -15px;
        -webkit-transition: color .5s;
        -o-transition: color .5s;
        transition: color .5s;
      }
    }

    h1 {
      color: #444;
      font-size: 20px;
      line-height: 50px;
      font-weight: 400;
    }

  }

  .title {
    float: left;
    font-size: 16px;
    margin-left: 8px;
    font-family: "Noto Sans SC";
    font-weight: 300;
  }

  .head .headlist {
    float: right;
    font-size: 14px;
    margin-right: 20px;

    .active {
      border-bottom: 1px solid #333435;
    }

    a {
      display: block;
      color: #333435;
      float: left;
      padding: 0 20px;
      -webkit-touch-callout: none;
      text-decoration: none;
      cursor: pointer;
      -moz-user-select: none;
      -webkit-user-select: none;
      -ms-user-select: none;
      user-select: none;

    }

    a:hover {
      color: #909090
    }
  }

  .head .btn {
    font-size: 14px;
    border: 2px solid #00a8ff;
    text-align: center;
    margin-top: 10px;
    height: 36px;
    line-height: 36px;
    border-radius: 42px;
    width: 120px;
    float: right;
    cursor: pointer;
  }

  .head .btn:hover {
    background-color: #00a8ff;
    color: #fff;
  }

  .head .content {
    width: 96%;
    margin: 0 auto;
    max-width: 1800px;
  }

  .head {
    line-height: 60px;
    height: 60px;
    width: 100%;
    background: hsla(0, 0%, 100%, 0.7);
    color: #333435;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    border-bottom: 1px solid #e5e5e5;
  }
</style>



